.header {
  text-transform: uppercase;
  justify-content: space-between;
  align-items: center;
  height: 60px;
  padding: 0 50px;
  font-size: 12px;
  font-weight: 500;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo-img {
  display: block;
}

.nav__list {
  gap: 20px;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item-link {
  color: #060b35;
  white-space: nowrap;
  align-items: center;
  height: 60px;
  font-weight: 500;
  text-decoration: none;
  transition: all .3s;
  display: flex;
}

.nav__item-link:hover {
  color: #00acdc;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.stars {
  gap: 4px;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.product-card {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
}

.product-card__img {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.product-card__title {
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  transition: all .3s;
}

.product-card:hover .product-card__title {
  color: #34568b;
}

.product-card__product-code {
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  line-height: 14px;
}

.product-card__reviews-container {
  justify-content: space-between;
  margin-bottom: 24px;
  font-size: 10px;
  line-height: 16px;
  display: flex;
}

.product-card__price-container {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  line-height: 18px;
  display: flex;
}

.product-card__price-value {
  font-size: 16px;
  font-weight: 700;
}

.product-card__price-text {
  color: #616070;
}

.product-card__button {
  text-transform: uppercase;
  color: #fff;
  box-sizing: border-box;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  justify-content: center;
  width: 100%;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 40px;
  text-decoration: none;
  transition: all .3s;
  display: flex;
}

.product-card__button:hover {
  color: #00acdc;
  background-color: #0000;
  border: 1px solid #00acdc;
}

* {
  margin: 0;
}

html {
  color: #060b35;
  font-family: Roboto, sans-serif;
  font-size: 12px;
}

.main {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media screen and (width >= 488px) {
  .main {
    grid-template-columns: repeat(2, 200px);
  }
}

@media screen and (width >= 768px) {
  .main {
    grid-template-columns: repeat(3, 200px);
  }
}

@media screen and (width >= 1024px) {
  .main {
    grid-template-columns: repeat(4, 200px);
  }
}

